.container{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: rgb(180, 172, 172);
    overflow: hidden;
    width: 100%;
    height: 100%;
}

.container-i{
    display: flex;
    padding-top: 60px ;
    width: 100%;
    justify-content:right;
    overflow: hidden;
    align-items: center;
    background-color: aqua;
    background-color: rgb(96, 100, 103);
}

.container-i input {
    width: 200px;
    height: 40px;
    margin-right: 100px;
    font-size: 16px;
    border: none;
    border-radius: 10px;
    padding-left: 20px;
    margin-bottom: 20px;
    background-color: white;
    outline: none;
}
.container-d{
    display: flex;
    align-items: center;
    justify-content:center;
    width: 100%;
    gap: 50px;
    margin: 50px 0px;
    width: 100%;
    height: 100%;
    flex-wrap: wrap;
    padding: 40px 0px;



}

.wrapper{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    width: 250px;
    height: 400px;
    border: none;
    border-radius: 10px;
    border: 1px solid rgb(136, 129, 129);
}

.wrapper img{
    width: 150px;
    height: 200px;
}


.details{
    font-size: 15px;
    margin-top: 20px;
    font-weight: 500;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    line-height: 0.1 ;

}

.loading {
    text-align: center;
    font-size: 1.2em;
    color: #555;
  }
  
  .error {
    text-align: center;
    font-size: 1.2em;
    color: #ff0000;
  }
  